"use client";

import { useRouter, useSearchParams } from "next/navigation";
import { CartItemsList } from "../types/product";
import { ArrowRight, Trash2 } from "lucide-react";
import { useState } from "react";
import ShoppingForm from "../features/cart/components/ShoppingForm";
import PaymentForm from "../features/cart/components/PaymentForm";
import Image from "next/image";
import OrderSummary from "../features/cart/components/OrderSummary";

const steps = [
  { id: 1, title: "Shopping Cart" },
  { id: 2, title: "Shipping address" },
  { id: 3, title: "Payment Method" },
];

const cartItems: CartItemsList = [
  {
    id: 1,
    name: "Nike Ultraboost Pulse",
    shortDescription: "راحة وأناقة بأحدث تكنولوجيا الجري.",
    description:
      "حذاء Nike Ultraboost Pulse مصمم بأحدث تقنيات الراحة والدعم. خامات عالية الجودة مع تهوية مثالية لجعل خطواتك أكثر خفة.",
    price: 69.9,
    sizes: ["s", "m", "l", "xl", "xxl"],
    colors: ["gray", "pink"],
    images: {
      gray: "/products/7g.png",
      pink: "/products/7p.png",
    },
    quantity: 1,
    selectedSize: "s",
    selectedColor: "gray",
  },
  {
    id: 2,
    name: "Levi's Classic Denim",
    shortDescription: "بنطلون جينز كلاسيكي يناسب كل الأوقات.",
    description:
      "جينز Levi's Classic Denim مصنوع من خامة متينة وجودة عالية، مناسب للخروج الكاجوال أو العمل مع تصميم أنيق ومريح.",
    price: 59.9,
    sizes: ["s", "m", "l"],
    colors: ["blue", "green"],
    images: {
      blue: "/products/2g.png",
      green: "/products/2gr.png",
    },
    quantity: 9,
    selectedSize: "m",
    selectedColor: " blue",
  },
  {
    id: 3,
    name: "Adidas Running Tee",
    shortDescription: "تيشيرت رياضي خفيف ومريح.",
    description:
      "تيشيرت Adidas مصنوع من قماش مرن وخفيف، يساعدك على الجري أو التمرين براحة تامة مع تهوية ممتازة.",
    price: 29.9,
    sizes: ["s", "m", "l", "xl"],
    colors: ["black", "white"],
    images: {
      black: "/products/3b.png",
      white: "/products/3bl.png",
    },
    quantity: 9,
    selectedSize: "xl",
    selectedColor: "black",
  },
];
export default function cartPage() {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [shippingForm, setShippingForm] = useState(null);
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const searchPaaram = useSearchParams();
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const router = useRouter();
  const activeStep = parseInt(searchPaaram.get("step") || "1");

  return (
    <div className=" flex flex-col gap-8 items-center justify-center mt-12">
      <h1 className="text-2xl font-medium">Your Shopping Cart</h1>
      <div className="flex flex-col gap-4 lg:flex-row  items-center">
        {steps.map((step) => (
          <div
            key={step.id}
            className={`flex gap-3 font-medium items-center
              border-b-2 pb-4
             ${
               activeStep === step.id
                 ? " border-gray-800"
                 : "border-gray-200 text-gray-400"
             }`}
          >
            <p
              className={`w-6 h-6 rounded-full flex justify-center items-center p-4
               ${
                 activeStep === step.id
                   ? "bg-gray-800 text-white"
                   : "bg-gray-200 text-gray-400"
               }`}
            >
              {step.id}
            </p>
            <p
              className={` font-medium text-sm
               ${activeStep === step.id ? " text-white" : " text-gray-400"}`}
            >
              {step.title}
            </p>
          </div>
        ))}
      </div>
      <div className="w-full flex flex-col lg:flex-row gap-16">
        {/* Steps */}
        <div
          className="w-full lg:w-7/12 shadow-lg border-1 border-amber-100 p-8 rounded-lg flex *:
          
          flex-col gap-8"
        >
          {activeStep === 1 ? (
            cartItems.map((item) => {
              // eslint-disable-next-line react/jsx-key
              return (
                // eslint-disable-next-line react/jsx-key
                <div className="flex items-center justify-between">
                  {/* IMge and Content */}
                  <div className="flex  gap-4">
                    <div className="relative w-32 h-32 rounded-lg overflow-hidden">
                      <Image
                        src={item.images[item.selectedColor]}
                        alt={item.name}
                        fill
                        className=" object-cover"
                      />
                    </div>
                    {/* Content */}
                    <div className="flex flex-col justify-between ">
                      <div className=" flx flex-col gap-1 text-gray-500 text-sm ">
                        <p>{item.name}</p>
                        <p> quantity :{item.quantity}</p>
                        <p>size : {item.selectedSize}</p>
                        <p>color : {item.selectedColor}</p>
                      </div>
                      <p className="text-sm font-medium">
                        ${item.price.toFixed(2)}
                      </p>
                    </div>
                  </div>
                  {/* Delete Button */}
                  <button
                    className=" gap-2 w-8 h-8  bg-gray-100 hover:bg-gray-200 transition-all duration-300 
                   p-2 py-1 rounded-full cursor-pointer text-red-400 "
                  >
                    <Trash2 className="w-4 h-4 flex items-center" />
                  </button>
                </div>
              );
            })
          ) : activeStep === 2 ? (
            <ShoppingForm />
          ) : activeStep === 3 && shippingForm ? (
            <PaymentForm />
          ) : (
            <p className="text-sm text-gray-400">
              Please fill the shipping form first
            </p>
          )}
        </div>
        {/* Order Summary */}
        <div
          className="w-full lg:w-7/12 shadow-lg border-1 border-amber-100 p-8 rounded-lg flex  h-max
          
          flex-col gap-8"
        >
          <h2 className="text-2xl font-medium">Order Summary</h2>
          <div className="flex flex-col gap-4">
            <div className="flex justify-between items-center text-sm">
              <p className="text-gray-500 ">subtotal</p>
              <span className="  font-medium ">
                {cartItems
                  .reduce((acc, item) => acc + item.price * item.quantity, 0)
                  .toFixed(2)}
              </span>
            </div>
            <div className="flex justify-between items-center text-sm">
              <p className="text-gray-500 ">shipping</p>
              <span className="  font-medium ">0</span>
            </div>
            <div className="flex justify-between items-center text-sm">
              <p className="text-gray-500 ">discount</p>
              <span className="  font-medium ">8%</span>
            </div>
            <hr className="border-1 border-gray-200" />
            <div className="flex justify-between items-center text-sm">
              <p className="text-gray-800 capitalize text-md  font-medium">
                total
              </p>
              <span className="  font-medium ">
                {cartItems
                  .reduce((acc, item) => acc + item.price * item.quantity, 0)
                  .toFixed(2)}
              </span>
            </div>
          </div>
          {activeStep === 1 && (
            <button
              onClick={() => router.push("/cart?step=2", { scroll: false })}
              className="bg-gray-700 hover:bg-gray-900 tarnsitin-all duration-300
           rounded-lg !cursor-pointer flex items-center justify-center  text-white p-2 px-4 w-full "
            >
              containue
              <ArrowRight className="w-4 h-4 ml-2" />
            </button>
          )}
        </div>
        <OrderSummary cartItems={cartItems} activeStep={activeStep} />
      </div>
    </div>
  );
}
